import { SchemaConfig } from '../types';
import { fetchCmsSchema } from './cms/cmsAdapter';

export async function getSchema(
  collectionId: string,
  entityTypeSource: string,
): Promise<SchemaConfig> {
  switch (entityTypeSource) {
    case 'cms':
      return fetchCmsSchema(collectionId);
    default:
      throw new Error(`Unsupported entityTypeSource: ${entityTypeSource}`);
  }
}
